.Heading {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 82px;
}

.Heading h2 {
    color: var(--, #333);
    font-size: 45px;
    font-weight: 800;
}

.Heading h1 {
    font-size: 55px;
    font-weight: 500;
    line-height: 65px;
    text-transform: uppercase;
}

.Heading p {
    font-weight: 600;
    line-height: 24px; /* 150% */
    margin-top: 14px;
}

.HeadingButtons {
    margin-top: 29px;
}

.Heading button {
    border-radius: 250px;
    background: #77B2D3;
    height: 54px;
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    line-height: 26px; /* 162.5% */;
}

.BigButton {
    width: 241px;
    margin-right: 10px;
}

.SmallButton {
    width: 114px;
    margin-right: 9px;
}

.AverageButton {
    width: 141px;
}