.Header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 14px;
}

.HeaderNav {
    display: flex;
    align-items: center;
    gap: 40px;
}

.HeaderButton {
    padding-top: 14px;
    padding-bottom: 14px;
    width: 205px;
    border-radius: 9px;
    background: var(--3, linear-gradient(115deg, #F32C6D 10.12%, #CB1D95 100.01%));
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    line-height: 26px; /* 162.5% */
}