.Consultate {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 90px;
}

.Consultate h2 {
    font-weight: 700;
}

.Consultate p {
    margin-top: 22px;
}

.Form {
    margin-top: 22px;
}

.Consultate input {
    font-size: 16px;
    font-weight: 300;
    line-height: 26px; /* 162.5% */;
    border: none;
    padding-left: 19px;
}

.SmallsInput {
    display: flex;
    gap: 30px;
}

.SmallInput {
    box-shadow: 0px 4px 18px 0px rgba(0, 0, 0, 0.08);
    width: 270px;
    height: 59px;
    border-radius: 7px;
    background: #fff;
}

.BigInput {
    width: 570px;
    height: 141px;
    border-radius: 7px;
    background: #FFF;
    box-shadow: 0px 4px 18px 0px rgba(0, 0, 0, 0.08);
    margin-top: 20px;
}

.ConsultateRadio {
    display: flex;
    gap: 10px;
    margin-top: 24px;
}

.RadioInput {
    margin-top: 20px;
    border-radius: 2px;
    background: #fff;
    box-shadow: 0px 4px 18px 0px rgba(0, 0, 0, 0.08);
    width: 32px;
    height: 32px;
}

.Consultate button {
    width: 195px;
    height: 54px;
    border-radius: 9px;
    background: var(--3, linear-gradient(115deg, #F32C6D 10.12%, #CB1D95 100.01%));
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    line-height: 26px; /* 162.5% */
    margin-top: 23px;
}
