.Facting {
    margin-top: 130px;
    padding-left: 55px;
    padding-right: 75px;
}

.Facting h2 {
    color: var(--, #333);
    font-size: 38px;
    font-weight: 550;
    line-height: 48px; /* 126.316% */
    text-transform: uppercase;
}

.FactingCard {
    display: flex;
    justify-content: space-between;
    margin-top: 35px;
}

.Facting h1 {
    font-weight: 800;
    line-height: 48px; 
    background: var(--3, linear-gradient(115deg, #F32C6D 10.12%, #CB1D95 100.01%));
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.H150 {
    font-size: 50px;
}

.H140 {
    font-size: 40px;
}

.H130 {
    font-size: 45px;
}

.Facting p {
    margin-top: 3px;
}


.FactingLine {
    content: '';
    width: 1px;
    height: 86px;
    background: #C7C7C7;
}