.ServiceCard {
    height: 453px;
}

.CardHeader {
    width: 220px;
    height: 74px;
    border-radius: 15px 15px 0px 0px;
    background-color: #77B2D3;
    box-shadow: 0px 4px 18px 0px rgba(0, 0, 0, 0.08);
}

.ServiceCard h5 {
    color: #fff;
    text-align: center;
    font-size: 18px;
    font-weight: 800;
    line-height: 26px; /* 144.444% */
    text-transform: uppercase;
    padding-top: 11px;
}

.CardText {
    margin-top: 18px;
}
.ServiceCard p {
    display: flex;
    align-items: center;
    gap: 5px;
    margin-top: 8px;
    margin-left: 16px;
}
.NoneImg {
    display: none;
}
.DisableImg {
    display: block;
}

.BigCardFooter {
    margin-top: 100px;
}

.SmallCardFooter {
    margin-top: 147px;
}

.ServiceCard h3 {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px; /* 150% */
}

.CardMuliColor {
    background: var(--3, linear-gradient(115deg, #F32C6D 10.12%, #CB1D95 100.01%));
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 30px;
    font-weight: 800;
    line-height: 24px;
}

.ServiceCard button {
    width: 195px;
    height: 54px;
    border-radius: 9px;
    background: var(--3, linear-gradient(115deg, #F32C6D 10.12%, #CB1D95 100.01%));
    color: #fff;
    font-size: 16px;
    font-weight: 600;
    line-height: 26px; /* 162.5% */
    margin-top: 36px;
}