.Footer {
    margin-top: 115px;
    padding-top: 52px;
    padding-bottom: 62px;
    background-color: #fff;
}

.FooterContent {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.Footer p {
    margin-bottom: 10px;
}

.Footer h4 {
    font-size: 16px;
    font-weight: 600;
    line-height: 36px; /* 225% */
    text-transform: uppercase;
    margin-bottom: 20px;
}

.FooterHref {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px; /* 150% */
    text-decoration-line: underline;
    margin-top: 5px;
}


